<template>
  <div id="mainHeader">
    <div class="container">
      <!--<span class="back" @click="goBack" v-if="this.$route.name !== 'login'">{{back}}</span>-->
      <span class="title">{{title}}</span>
    </div>
  </div>
</template>

<script>
export default {
  name: 'mainHeader',
  data () {
    return {
      title: this.$route.meta.title,
      back: '<'
    }
  },
  watch: {
    '$route' (val) {
      this.title = this.$route.meta.title
    }
  },
  methods: {
    goBack () {
      this.$router.go(-1)
    }
  }
}
</script>

<style scoped lang="scss">
  .container {
    height: .76rem;
    line-height: .76rem;
    background: #FC7D3D;
    color: #fff;
    text-align: center;
    position: relative;
    z-index: 100;
  }
  .back {
    float: left;
    font-size: .52rem;
    width: .8rem;
    text-align: left;
    padding-left: .2rem;
  }
  .title {
    font-size: .36rem;
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    -o-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
  }
</style>



